export default ".linear-gradient {\n  width: 100%;\n  height: 100%;\n  background: linear-gradient(to left top, #3f606b 55%, #ffffff 40%);\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  overflow: auto;\n}\n\n.wrapper {\n  border: 1px solid #efecec;\n  border-radius: 4px;\n  width: 100%;\n  margin: 5px 0;\n  background-color: #fff;\n}\n\n.login-title {\n  padding: 5px;\n  font-size: 13px;\n  margin: 0;\n  position: absolute;\n  left: 40px;\n  top: 30px;\n}\n\n.login-field {\n  display: flex;\n  align-items: baseline;\n  margin: 0;\n  padding: 2px 3px;\n}\n\n.login-field p {\n  width: 65%;\n  padding: 0 6px;\n}\n\n.form {\n  width: 400px;\n  border: 1px solid #3f606b;\n  border-radius: 0;\n  box-shadow: 0px 0px 0px 2px white;\n}\n\n.form .form-fields {\n  padding: 2px 3px;\n}\n\n.form-message {\n  flex-direction: column;\n  padding: 15px;\n  padding-top: 20px;\n  width: 100%;\n  background-color: white;\n  border-bottom: 1px solid #e8e8e8;\n}\n\n#exercheckbox {\n  margin-right: 8px;\n  cursor: pointer;\n}\n\n#reps {\n  display: none;\n}\n\n#exercheckbox:checked ~ #reps {\n  display: block;\n}\n\n.checkbox-bg {\n  display: flex;\n  align-items: center;\n  padding: 10px 0 !important;\n  justify-content: flex-start !important;\n}\n\n.checkbox-bg > label {\n  margin: 0;\n}\n\n.checkbox-bg .toggle-btn {\n  min-width: 36px;\n  max-width: 36px;\n  height: 18px;\n  border-radius: 50px;\n  position: relative;\n  background: #5a6f88;\n  cursor: pointer;\n  transition: background-color 0.15s ease-in-out;\n  margin: 0px;\n  padding: 0;\n}\n\n.checkbox-bg .toggle-btn::before {\n  content: \"\";\n  width: 16px;\n  height: 16px;\n  background-color: #fff;\n  border-radius: 50%;\n  position: absolute;\n  top: 1px;\n  left: 1px;\n  transition: all 0.15s ease-in-out;\n  box-shadow: inset 0px 0px 1px 0px rgba(0, 0, 0, 0.9);\n  z-index: 2;\n}\n\n.checkbox-bg .checkbox-bg-text {\n  margin-left: 7px !important;\n  cursor: pointer;\n  padding: 0;\n}\n\n.checkbox-bg