import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';

import { YesNoFormComponent } from '@shared/forms-dinamic/yes-no-form/yes-no-form.component';

import { TasksService } from '../all-tasks/tasks.service';



@Component({
  selector: 'vdi-info-task',
  templateUrl: './info-tasks.component.html',
  styleUrls: ['./info-tasks.component.scss']
})
export class InfoTaskComponent {

  task: any;

  public collection: any[] = [
    {
      title: '',
      property: 'message',
      type: 'string'
    },
    {
      title: '',
      property: 'task_type',
      type: 'task_type',
    },
    {
      title: '',
      property: 'status',
      type: 'string',
    },
    {
      title: ' ',
      property: 'started',
      type: 'time'
    },
    {
      title: ' ',
      property: 'finished',
      type: 'time'
    },
    {
      title: '',
      property: 'duration',
      type: 'string'
    }
  ];

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private service: TasksService,
    public dialog: MatDialog
  ) {
    this.task = data.task;
  }

  public cancelTask() {
    this.dialog.open(YesNoFormComponent, {
      disableClose: true,
      width: '500px',
      data: {
        form: {
          header: ' ',
          question: ' ?',
          button: ''
        },
        request: {
          service: this.service,
          action: 'cancelTask',
          body: {
            task: this.task.id
          }
        }
      }
    });
  }
}
