import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'rename'
})
export class TranslatePipe implements PipeTransform {

  constructor() {}

  transform(name) {
    let translate = '';

    if (name === 'FOLDERS_REDIR') {
      translate = ' ';
    } else if (name === 'SHARED_CLIPBOARD') {
      translate = '  ';
    } else if (name === 'USB_REDIR') {
      translate = ' USB';
    } else  {
      translate = name;
    }
    return translate;
  }
}
