  public vnc: any;
  public spice: any;
  public show: boolean = false;

  constructor(
    public dialog: MatDialog,
    private sanitizer: DomSanitizer,
    private waitService: WaitService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private service: PoolDetailsService
  ) {
    this.waitService.setWait(true);
  }

  ngOnInit() {
    this.service.getVm(this.data.idPool, this.data.vmActive, this.data.controller_id).valueChanges.subscribe((res) => {
      this.data = { ...this.data, vm: res.data.pool.vm }
      this.show = true;
      this.waitService.setWait(false);
    })
  }

  public refresh() {
    this.show = false;
    this.waitService.setWait(true);
    this.service.getVm(this.data.idPool, this.data.vmActive, this.data.controller_id).refetch();
  }

  public addUser() {
    this.dialog.open(AddUserVmComponent, {
      disableClose: true,
      width: '500px',
      data: this.data
    });
  }

  public removeUser() {
    this.dialog.open(RemoveUserVmComponent, {
      disableClose: true,
      width: '500px',
      data: this.data
    });
  }

  public clickRow(backup): void {
    this.openBackupDetails(backup);
  }

  public clickEvent(event): void {
    this.openEventDetails(event);
  }

  public openEventDetails(event: Event): void {
    this.dialog.open(InfoEventComponent, {
      disableClose: true,
      width: '700px',
      data: {
        event
      }
    });
  }

  public openBackupDetails(backup: Backup): void {
    this.dialog.open(InfoBackupComponent, {
      disableClose: true,
      width: '700px',
      data: {
        backup
      }
    });
  }

  public test(): void {
    this.testing = true;
    this.service.testDomainVm(
        this.data.vm.id
      )
      .subscribe((data) => {
        if (data) {
          setTimeout(() => {
            this.testing = false;
            this.tested = true;
            this.connected = data.data.testDomainVm.ok;
          }, 1000);

          setTimeout(() => {
            this.tested = false;
          }, 5000);
        } else {
          this.testing = false;
          this.tested = false;
        }
      });
  }

  public routeTo(route: string): void {
    if (route === 'vnc') {
      this.show = false;
      this.waitService.setWait(true)

      this.service.getVnc(this.data.idPool, this.data.vmActive, this.data.controller_id).valueChanges.subscribe((res) => {
        const vnc: any = res.data.pool.vm.vnc_connection
        const prot = window.location.protocol;

        let port = 80;

        if (prot === 'https:') {
          port = 443;
        }

        if (vnc.port) {
          port = vnc.port;
        }

        this.vnc = this.sanitizer.bypassSecurityTrustResourceUrl(`novnc/vnc.html?host=${vnc.host}&port=${port}&password=${vnc.password}&path=websockify?token=${vnc.token}`);

        this.show = true;
        this.waitService.setWait(false)
      });
    }

    if (route === 'spice') {
      this.show = false;
      this.waitService.setWait(true)

      this.service.getSpice(this.data.idPool, this.data.vmActive, this.data.controller_id).valueChanges.subscribe((res) => {
        const spice: any = res.data.pool.vm.spice_connection

        this.spice = this.sanitizer.bypassSecurityTrustResourceUrl(spice.connection_url);

        this.show = true;
        this.waitService.setWait(false)
      });
    }

    this.menuActive = route;
  }

  public action(type) {
    if (type === 'start') {
      this.dialog.open(YesNoFormComponent, {
        disableClose: true,
        width: '500px',
        data: {
          form: {
            header: ' ',
            question: ' ?',
            button: ''
          },
          request: {
            service: this.service,
            action: 'startVm',
            body: {
              vm_id: this.data.vm.id
            }
          }
        }
      })
    }

    if (type === 'suspend') {
      this.dialog.open(YesNoFormComponent, {
        disableClose: true,
        width: '500px',
        data: {
          form: {
            header: ' ',
            question: ' ?',
            button: ''
          },
          request: {
            service: this.service,
            action: 'suspendVm',
            body: {
              vm_id: this.data.vm.id
            }
          }
        }
      })
    }

    if (type === 'shutdown') {
      this.dialog.open(YesNoFormComponent, {
        disableClose: true,
        width: '500px',
        data: {
          form: {
            header: ' ',
            question: ' ?',
            button: ''
          },
          request: {
            service: this.service,
            action: 'shutdownVm',
            body: {
              vm_id: this.data.vm.id,
              force: false
            }
          }
        }
      })
    }

    if (type === 'reboot') {
      this.dialog.open(YesNoFormComponent, {
        disableClose: true,
        width: '500px',
        data: {
          form: {
            header: ' ',
            question: ' ?',
            button: ''
          },
          request: {
            service: this.service,
            action: 'rebootVm',
            body: {
              vm_id: this.data.vm.id,
              force: false
            }
          }
        }
      })
    }

    if (type === 'shutdown-force') {
      this.dialog.open(YesNoFormComponent, {
        disableClose: true,
        width: '500px',
        data: {
          form: {
            header: ' ',
            question: '  ?',
            button: ''
          },
          request: {
            service: this.service,
            action: 'shutdownVm',
            body: {
              vm_id: this.data.vm.id,
              force: true
            }
          }
        }
      })
    }

    if (type === 'reboot-force') {
      this.dialog.open(YesNoFormComponent, {
        disableClose: true,
        width: '500px',
        data: {
          form: {
            header: ' ',
            question: '  ?',
            button: ''
          },
          request: {
            service: this.service,
            action: 'rebootVm',
            body: {
              vm_id: this.data.vm.id,
              force: true
            }
          }
        }
      })
    }

    if (type === 'backup') {
      this.dialog.open(YesNoFormComponent, {
        disableClose: true,
        width: '500px',
        data: {
          form: {
            header: ' ',
            question: '   ?',
            button: ''
          },
          request: {
            service: this.service,
            action: 'backupVm',
            body: {
              vm_id: this.data.vm.id,
              force: true
            }
          }
        }
      })
    }
  }

  public prepareVM() {
    if (this.data.typePool === 'automated' || this.data.typePool === 'guest') {
      this.dialog.open(YesNoFormComponent, {
        disableClose: true,
        width: '500px',
        data: {
          form: {
            header: ' ',
            question: `  ${this.data.vm.verbose_name}?`,
            button: ''
          },
          request: {
            service: this.service,
            action: 'prepareVm',
            body: {
              vm: this.data.vm.id
            }
          }
        }
      })
    }
  }

