  public toPage(message: any): void {
    this.offset = message.offset;
    this.service.getPool(this.data.idPool, this.data.typePool).refetch();
  }

  public convertToTemplate(): void {

    this.dialog.open(ConvertToTemaplteComponent, {
      disableClose: true,
      width: '500px',
      data: {
        vm_id: this.data.vm.id,
        controller_id: this.data.vm.controller.id,
        pool: this.data
      }
    });
  }

  public toggleReserve(e) {

    e.preventDefault();

    if (this.data.vm.status === 'RESERVED') {
      this.activateVm();
    } else {
      this.reserveVm();
    }
  }

  public reserveVm() {
    this.dialog.open(YesNoFormComponent, {
      disableClose: true,
      width: '500px',
      data: {
        form: {
          header: ' ',
          question: `  ${this.data.vm.verbose_name}   ""?`,
          button: ''
        },
        request: {
          service: this.service,
          action: 'reserveVm',
          body: {
            vm_id: this.data.vm.id,
            reserve: true,
          }
        }
      }
    }).afterClosed().subscribe(() => {
      this.service.getPool(this.data.idPool, this.data.typePool).refetch();
      this.service.getVm(this.data.idPool, this.data.vm.id, this.data.controller_id).refetch();
    })
  }

  public activateVm() {
    this.dialog.open(YesNoFormComponent, {
      disableClose: true,
      width: '500px',
      data: {
        form: {
          header: ' ',
          question: `  ${this.data.vm.verbose_name}?`,
          button: ''
        },
        request: {
          service: this.service,
          action: 'reserveVm',
          body: {
            vm_id: this.data.vm.id,
            reserve: false,
          }
        }
      }
    }).afterClosed().subscribe(() => {
      this.service.getPool(this.data.idPool, this.data.typePool).refetch();
      this.service.getVm(this.data.idPool, this.data.vm.id, this.data.controller_id).refetch();
    })
  }
