import { Component, OnInit, OnDestroy } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import {YesNoFormComponent} from '../../../../shared/forms-dinamic/yes-no-form/yes-no-form.component';
import { TemplatesService } from '../all-templates/templates.service';

interface ICollection {
  [index: string]: string;
}

@Component({
  selector: 'vdi-template-details',
  templateUrl: './template-details.component.html',
  styleUrls: ['./template-details.component.scss']
})


export class TemplateDetailsComponent implements OnInit, OnDestroy {

  public template: ICollection = {};

  public collectionDetails = [
    {
      title: '',
      property: 'verbose_name',
      class: 'name-start',
      icon: 'desktop',
      type: 'string'
    },
    {
      title: '',
      property: 'description',
      type: 'string'
    },
    {
      title: '',
      property: 'cpu_count',
      type: 'string'
    },
    {
      title: ' ',
      property: 'memory_count',
      type: 'bites',
      delimiter: ''
    },
    {
      title: ' ',
      property: 'os_type',
      type: 'string'
    },
    {
      title: '  ',
      property: 'os_version',
      type: 'string'
    },
    {
      title: ' ',
      property: 'resource_pool',
      property_lv2: 'verbose_name',
      type: 'string'
    },
    {
      property: 'tablet',
      title: ' ',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      property: 'spice_stream',
      title: 'SPICE ',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      property: 'ha_enabled',
      title: ' ',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      property: 'disastery_enabled',
      title: '',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      property: 'remote_access',
      title: ' ',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      property: 'start_on_boot',
      title: '  ',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      title: '  ',
      property: 'boot_type',
      type: 'string'
    },
    {
      title: '',
      property: 'status',
      type: 'string'
    },
    {
      title: '',
      property: 'domain_tags',
      type: {
        typeDepend: 'tags_array'
      }
    }
  ];

  public idTemplate: string;
  public menuActive: string = 'info';
  public host: boolean = false;
  private address: string;
  private sub: Subscription;

  constructor(private activatedRoute: ActivatedRoute,
              private service: TemplatesService,
              private router: Router,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe((param: ParamMap) => {
      this.idTemplate = param.get('id') as string;
      this.address = param.get('address') as string;
      this.getTemplate();
    });
  }

  public getTemplate() {
    if (this.sub) {
      this.sub.unsubscribe();
    }
    this.host = false;
    this.sub = this.service.getTemplate(this.idTemplate, this.address).valueChanges.pipe(map(data => data.data.template))
      .subscribe((data) => {
        this.template = data
        this.host = true;
      },
      () => {
        this.host = true;
      });
  }

  public attachVeilUtils() {
    this.dialog.open(YesNoFormComponent, {
        disableClose: true,
        width: '500px',
        data: {
          form: {
            header: ' ',
            question: `  VeiL utils   ${this.template.verbose_name}?`,
            button: ''
          },
          request: {
            service: this.service,
            action: 'attachVeilUtils',
            body: {
              id: this.idTemplate,
              controller_id: this.address
            }
          }
        }
      })
  }

  public close() {
    this.router.navigate(['pages/resourses/templates']);
  }

  public routeTo(route: string): void {
    if (route === 'info') {
      this.menuActive = 'info';
    }
  }

  ngOnDestroy() {
    if (this.sub) {
      this.sub.unsubscribe();
    }
  }
}
