import { Injectable } from '@angular/core';
import { Apollo, QueryRef } from 'apollo-angular';
import gql from 'graphql-tag';

import { IParams } from '@shared/types';

/**
 *   : 1) onInit() spin:true
 *                   2) sort spin:true,nameSort:new
 *                   3)  : / , /    , /   .
 *                           spin:false,nameSort:old
 *                   4)  refresh spin:true,nameSort:old
 */

@Injectable()
export class PoolsService {

    public paramsForGetPools: IParams = { //       
        spin: true,
        nameSort: undefined
    };

    constructor(private service: Apollo) { }

    public getAllControllers(): QueryRef<any, any> {
        return this.service.watchQuery({
            query: gql`
                query controllers {
                    controllers {
                        id
                    }
                }
            `,
            variables: {
                method: 'GET'
            }
        });
    }

    public getAllPools(): QueryRef<any, any> {


        return this.service.watchQuery({
            query: gql` query pools($ordering:ShortString) {
                            pools(ordering: $ordering) {
                                pool_id
                                verbose_name
                                vm_amount
                                pool_type
                                controller {
                                    verbose_name
                                    address
                                }
                                users {
                                    username
                                }
                                status
                            }
                        }
                `,
            variables: {
                method: 'GET',
                ordering: this.paramsForGetPools.nameSort
            }
        });
    }
}

