import { Component, OnInit, OnDestroy } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import {YesNoFormComponent} from '../../../../shared/forms-dinamic/yes-no-form/yes-no-form.component';
import { VmsService } from '../all-vms/vms.service';

interface ICollection {
  [index: string]: string;
}

@Component({
  selector: 'vdi-vm-details',
  templateUrl: './vm-details.component.html',
  styleUrls: ['./vm-details.component.scss']
})


export class VmDetailsComponent implements OnInit, OnDestroy {

  public vm: ICollection = {};

  public collectionDetails = [
    {
      title: '',
      property: 'verbose_name',
      class: 'name-start',
      icon: 'desktop',
      type: 'string'
    },
    {
      title: '',
      property: 'description',
      type: 'string'
    },
    {
      title: '',
      property: 'user_power_state',
      type: 'string'
    },
    {
      title: 'IP ',
      property: 'address',
      type: 'array'
    },
    {
      title: ' ',
      property: 'hostname',
      type: 'string'
    },
    {
      title: '',
      property: 'cpu_count',
      type: 'string'
    },
    {
      title: ' ',
      property: 'memory_count',
      type: 'bites',
      delimiter: ''
    },
    {
      title: ' ',
      property: 'os_type',
      type: 'string'
    },
    {
      title: '  ',
      property: 'os_version',
      type: 'string'
    },
    {
      title: '',
      property: 'parent_name',
      type: 'string'
    },
    {
      title: ' ',
      property: 'resource_pool',
      property_lv2: 'verbose_name',
      type: 'string'
    },
    {
      property: 'tablet',
      title: ' ',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      property: 'spice_stream',
      title: 'SPICE ',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      property: 'ha_enabled',
      title: ' ',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      property: 'disastery_enabled',
      title: '',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      property: 'guest_agent',
      title: ' ',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      property: 'remote_access',
      title: ' ',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      property: 'start_on_boot',
      title: '  ',
      type: {
        typeDepend: 'boolean',
        propertyDepend: ['', '']
      }
    },
    {
      title: '  ',
      property: 'boot_type',
      type: 'string'
    },
    {
      title: '',
      property: 'status',
      type: 'string'
    },
    {
      title: '',
      property: 'domain_tags',
      type: {
        typeDepend: 'tags_array'
      }
    }
  ];

  public idVm: string;
  public menuActive: string = 'info';
  public host: boolean = false;
  private address: string;
  private sub: Subscription;

  constructor(private activatedRoute: ActivatedRoute,
              private service: VmsService,
              private router: Router,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe((param: ParamMap) => {
      this.idVm = param.get('id') as string;
      this.address = param.get('address');
      this.getVM();
    });
  }

  public getVM() {
    if (this.sub) {
      this.sub.unsubscribe();
    }
    this.host = false;
    this.sub = this.service.getVm(this.idVm, this.address).valueChanges.pipe(map(data => data.data.vm))
      .subscribe((data) => {
        this.vm = data;
        this.host = true;
      },
      () => {
        this.host = true;
      });
  }

  public attachVeilUtils() {
    this.dialog.open(YesNoFormComponent, {
        disableClose: true,
        width: '500px',
        data: {
          form: {
            header: ' ',
            question: `  VeiL utils   ${this.vm.verbose_name}?`,
            button: ''
          },
          request: {
            service: this.service,
            action: 'attachVeilUtils',
            body: {
              id: this.idVm,
              controller_id: this.address
            }
          }
        }
      })
  }

  public close() {
    this.router.navigate(['pages/resourses/vms']);
  }

  public routeTo(route: string): void {
    if (route === 'info') {
      this.menuActive = 'info';
    }
  }

  ngOnDestroy() {
    if (this.sub) {
      this.sub.unsubscribe();
    }
  }
}
